<template>
  <div>
      <div class="selectWrapper" id="selectUser">
        <select class="user-select" @change="onChange($event)">
            <option v-for="usuarios in this.nomesId" v-bind:key="usuarios.id" :value="usuarios.id"> User {{usuarios.nome}}</option>
        </select>
      </div>
      <div class="row view">
        <div class="posts">
          <h2>Posts</h2>
          <ul v-for="item in this.lista[this.usuario]" v-bind:key="item.title">
            <li>{{item.title}}</li>
          </ul>
        </div>
        <div class="criar">
          <h2>Create/Edit Post</h2>
          <form action="POST">
            <div>
              <input class="textar" type="text" placeholder="Title post" v-model="this.titulo">
            </div>
            <div>
              <textarea class="textar" name="frontend" id="" cols="60" rows="15" v-model="this.corpo"></textarea>
            </div>
            <input class="textar" type="button" value="Salvar" v-on:click="salvar(this.usuario)" style="padding: 5px">
          </form>

        </div>
      </div>
  </div>
</template>

<style>
