<script>


export default {
  name: 'PostVue',
  props: {
    msg: String,
    
  },
  data(){
    return {
      users: [],
      lista: [],
      names: [],
      nomesId: [],
      usernames: [],
      emails: [],
      phones: [],
      streets: [],
      cities: [],
      userSelect: 'selectUser',
      usuario: 0,
      corpo: '',
      titulo: ''
    }
  },
  mounted(){
    this.pegaLista();
    this.pegaUsuario()


  },
  methods: {
    pegaLista(){
      fetch('https://jsonplaceholder.typicode.com/posts')
      .then((response) => response.json())
      .then((json) => {
        this.users = json;
        let i;
        for(i = 1; i <= 10; i++){
          let filtered = this.users.filter(function(el){
          return el.userId === i;
        })
        this.lista.push(filtered)
        filtered = []
        }        
      })

    },
    pegaUsuario(){
      fetch('https://jsonplaceholder.typicode.com/users')
      .then((response) => response.json())
      .then((json) => {
        this.users = json;
        let i;
        for(i = 0; i < 10; i++){
            this.names.push(this.users[i].name)
            this.usernames.push(this.users[i].username)
            this.emails.push(this.users[i].email)
            this.phones.push(this.users[i].phone)
            this.streets.push(this.users[i].address.street)
            this.cities.push(this.users[i].address.city)
        }
        this.nomesId = this.names.map((str, index)=> ({nome: str, id: index +1}))
        
      })

    },
    onChange(event){
      this.usuario = event.target.value;
    },

    salvar(userid){
      this.lista[this.usuario].push({
        userId: userid,
        id: this.lista.length +1,
        title: this.titulo,
        body: this.corpo
      })
      alert('Salvo com sucesso')
      this.titulo = '';
      this.corpo = null
      
    }
  }
}
</script>

